/* Style du tableau */
table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
}

th, td {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: left;
}

th {
    background-color: #f4f4f4;
}

/* Organisation des boutons */
.action-buttons {
    display: flex;
    gap: 8px;
}

/* Styles des boutons */
.view-btn, .edit-btn, .details-btn {
    padding: 5px 10px;
    border: none;
    cursor: pointer;
    border-radius: 4px;
    font-size: 14px;
}

/* Couleurs spÃ©cifiques */
.view-btn {
    background-color: #007bff;
    color: white;
}

.edit-btn {
    background-color: #28a745;
    color: white;
}

.details-btn {
    background-color: #ffc107;
    color: black;
}

/* Effet au survol */
.view-btn:hover, .edit-btn:hover, .details-btn:hover {
    opacity: 0.8;
}